
import {
    provide,
    reactive,
    defineComponent,
    computed,
    ref,
    Ref,
    toRef,
    toRefs,
    watch,
    nextTick,
} from 'vue'
import Draggable from 'vuedraggable'
import Popup from '@/components/popup/index.vue'
import FileItem from './file-item.vue'
import Material from './material.vue'
export default defineComponent({
    components: {
        Popup,
        Draggable,
        FileItem,
        Material,
    },
    props: {
        modelValue: {
            type: [String, Array],
            default: () => [],
        },
        // 文件类型
        type: {
            type: String,
            default: 'image',
        },
        // 选择器尺寸
        size: {
            type: String,
            default: '100px',
        },
        // 文件尺寸
        fileSize: {
            type: String,
            default: '100px',
        },
        // 选择数量限制
        limit: {
            type: Number,
            default: 1,
        },
        // 禁用选择
        disabled: {
            type: Boolean,
            default: false,
        },
    },

    emits: ['change', 'update:modelValue'],
    setup(props, { emit }) {
        const popupRef: Ref<typeof Popup | null> = ref(null)
        const materialRefs: Ref<typeof Material | null> = ref(null)
        const fileList: Ref<any[]> = ref([])
        const select: Ref<any[]> = ref([])
        const isAdd = ref(true)
        const currentIndex = ref(-1)
        const { disabled, limit, modelValue } = toRefs(props)
        const tipsText = computed(() => {
            switch (props.type) {
                case 'image':
                    return '图片'
                case 'video':
                    return '视频'
            }
        })
        const typeValue = computed(() => {
            switch (props.type) {
                case 'image':
                    return 10
                case 'video':
                    return 20
                case 'file':
                    return 30
            }
        })
        const showUpload = computed(() => {
            return props.limit - fileList.value.length > 0
        })
        const meterialLimit = computed(() => {
            if (!isAdd.value) {
                return 1
            }
            if (!limit.value) return null
            return limit.value - fileList.value.length
        })
        const handleConfirm = () => {
            const selectUri = select.value.map((item) => item.uri)
            if (!isAdd.value) {
                fileList.value.splice(currentIndex.value, 1, selectUri.shift())
            } else {
                fileList.value = [...fileList.value,...selectUri]
            }
            handleChange()
        }
        const showPopup = (index: number) => {
            if (disabled.value) return
            if (index >= 0) {
                isAdd.value = false
                currentIndex.value = index
            } else {
                isAdd.value = true
            }
            popupRef.value?.open()
        }

        const selectChange = (val: any[]) => {
            select.value = val
        }
        const handleChange = () => {
            const valueImg =
                limit.value != 1 ? fileList.value : fileList.value[0] || ''
            emit('update:modelValue', valueImg)
            emit('change', valueImg)
            nextTick(() => {
                materialRefs.value?.clearSelect()
            })
        }

        const deleteImg = (index: number) => {
            fileList.value.splice(index, 1)
            handleChange()
        }

        watch(modelValue, (val: any[] | string) => {
            console.log(val)
            fileList.value = Array.isArray(val) ? val : val == '' ? [] : [val]
        })
        provide('type', props.type)
        provide('fileSize', props.fileSize)
        provide('limit', props.limit)
        provide('typeValue', typeValue)
        return {
            popupRef,
            materialRefs,
            fileList,
            tipsText,
            handleConfirm,
            meterialLimit,
            showUpload,
            showPopup,
            selectChange,
            deleteImg
        }
    },
})
