
import { defineComponent } from "vue"
export default defineComponent({
    components: {},
    emits: ['close'],
    props: {
        showClose: {
            type: Boolean,
            default: true
        }
    },
    setup(props, { emit }) {
        const handleClose = () => {
            emit('close')
        }
        return {
            handleClose
        }
    },
})
