
import { defineComponent, inject, Ref, ref, toRefs, watch } from 'vue'
import { useCate, useFile } from './hook'
import PopoverInput from '@/components/popover-input/index.vue'
import Pagination from '@/components/pagination/index.vue'
import Popup from '@/components/popup/index.vue'
import Upload from '@/components/upload/index.vue'
import FileItem from './file-item.vue'
import { Search } from '@element-plus/icons-vue'
import { ElTree } from 'element-plus'
export default defineComponent({
    components: {
        PopoverInput,
        Pagination,
        Popup,
        Upload,
        FileItem,
    },
    props: {
        fileSize: {
            type: String,
            default: '100px',
        },
        limit: {
            type: Number,
            default: 1,
        },
    },
    emits: ['change'],
    setup(props, { emit }) {
        const treeRefs: Ref<typeof ElTree | null> = ref(null)
        const type = inject('type') as Ref<string>
        const { limit } = toRefs(props)
        const typeValue = inject('typeValue') as Ref<10 | 20 | 30>
        const visible = inject('visible') as Ref<boolean>
        const {
            cateId,
            cateLists,
            handleAddCate,
            handleEditCate,
            handleDeleteCate,
            getCateLists,
        } = useCate(typeValue)
        const {
            moveId,
            pager,
            fileParams,
            select,
            getFileList,
            refresh,
            batchFileDelete,
            batchFileMove,
            selectFile,
            selectStatus,
            clearSelect,
            cancelSelete,
        } = useFile(cateId, typeValue, limit)

        const currentChange = (item: any) => {
            cateId.value = item.id
        }

        watch(
            visible,
            async (val: boolean) => {
                if (val) {
                    await getCateLists()
                    treeRefs.value?.setCurrentKey(cateId.value)
                    getFileList()
                }
            },
            {
                immediate: true,
            }
        )
        watch(cateId, (val: string) => {
            fileParams.name = ''
            refresh()
        })
        watch(
            select,
            (val: any[]) => {
                emit('change', val)
            },
            {
                deep: true
            }
        )
        return {
            treeRefs,
            Search,
            type,
            limit,
            cateId,
            cateLists,
            handleAddCate,
            handleEditCate,
            handleDeleteCate,
            currentChange,
            moveId,
            pager,
            fileParams,
            select,
            getFileList,
            refresh,
            batchFileDelete,
            batchFileMove,
            selectFile,
            selectStatus,
            clearSelect,
            cancelSelete,
        }
    },
})
