
import { defineComponent, ref } from 'vue'
export default defineComponent({
    props: {
        type: {
            type: String,
            default: 'number',
        },
        width: {
            type: Number,
            default: 250,
        },
        placeholder: {
            type: String,
            default: '',
        },
        disabled: {
            type: Boolean,
            default: false,
        },
    },
    emits: ['confirm'],
    setup(props, { emit }) {
        const visible = ref(false)
        const value = ref('')
        const open = () => {
            if (props.disabled) return
            visible.value = true
        }
        const close = () => {
            visible.value = false
        }

        const handleConfirm = () => {
            if (value.value) {
                emit('confirm', value.value)
                value.value = ''
            }
            close()
        }
        return {
            visible,
            value,
            open,
            close,
            handleConfirm,
        }
    },
})
